.home {
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

.header {
  z-index: 1;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}
.p1 {
  color: black;
  display: inline-block;
  line-height: 1;
}

.react-rotating-text-cursor {
  animation: blinking-cursor 0.7s cubic-bezier(0.68, 0.01, 0.01, 0.99) 0s
    infinite;
}

@keyframes blinking-cursor {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
