/* Line through */
.head {
  display: flex;
  flex-direction: row;
  width: 30%;
  margin: auto;
}
.head:before,
.head:after {
  content: "";
  flex: 1 1;
  border-bottom: 1px solid #191919;
  margin: auto;
}
.head:before {
  margin-right: 20px;
}
.head:after {
  margin-left: 20px;
}
@media only screen and (max-width: 768px) {
  .head {
    width: 80%;
  }
}
.progress {
  height: 0.3rem;
  border-radius: 30px;
}
